.home {
  position: relative;
  display: flex;
  z-index: 9;
  .home-bg {
    position: absolute;
    width: 22%;
    padding-bottom: 70%;
    background-color: var(--deep-blue);
    border-radius: 0 50px 50px 0;
    top: -2%;
    left: 0;
  }
  .home-contaniner {
    flex: 1;
    margin: 6%;
    z-index: 1;
    .header {
      margin: 0 3%;
      padding-bottom: 30%;
      position: relative;
      .header-container {
        position: absolute;
        display: flex;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        .userAvatar {
          flex: 1;
          height: 100%;
          position: relative;
          .userAvatar-webIcon {
            position: absolute;
            bottom: 13%;
            width: 20%;
            padding-bottom: 20%;
            border-radius: 10px;
            background-color: #FF9A3E;
            box-shadow: var(--box-shadow);
            z-index: 9;
            overflow: hidden;
            img {
              position: absolute;
              width: 100%;
              height: auto;
              left: 7%;
              top: 9%;
            } 
          }
          .userAvatar-img {
            width: 75%;
            height: 100%;
            padding: 0 6% 0 10%;
            position: relative;
            .userAvatar-img-frame {
              width: 100%;
              height: 100%;
              border-radius: 1vw;
              overflow: hidden;
              img {
                // width: 100%;
                height: 100%;
                // object-fit: cover;
                transform: translateX(-20%);
              }
            }
            .userAvatar-icon {
              position: absolute;
              width: 17%;
              padding-bottom: 17%;
              top: 10%;
              right: 0;
              z-index: 1;
              // font-size: 25px;
              color: white;
              background-color: #0077FF;
              // padding: 2%;
              border-radius: 10px;
              box-shadow: var(--box-shadow);
              div {
                font-size: 3vw;
                font-weight: bolder;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-80%);
                // transform: translateY(-28%);
              }
            } 
          }
        }
        .userInfo {
          flex: 1;
          height: 100%;
          white-space: nowrap;
          .userInfo-container {
            margin: 0 0 0 -15%;
            padding-right: 15%;
            .userInfo-container-title {
              margin-top: 2%;
              font-size: 10vw;
              color: white;
              text-shadow: var(--box-shadow);
            }
            .userInfo-container-subTitle {
              display: flex;
              margin: 1.5% 0 5% 0;
              .userInfo-container-subTitle-blog {
                font-size: 0.3rem;
                color: var(--main-color);
                padding: 1.5% 2%;
                margin-left: 3%;
                border-radius: 10px;
                box-shadow: inset var(--box-shadow);
                text-shadow: var(--box-shadow);
                background-color: white;
              }
              .userInfo-container-subTitle-text {
                flex: 1;
                // padding-right: 20%;
                font-size: 0.13rem;
                color: white;
                display: flex;
                flex-direction: column;
                align-items: flex-end;
                justify-content: space-around;
                .userInfo-container-subTitle-text-button {
                  color: var(--main-color);
                  background-color: white;
                  padding: 1% 1%;
                  border-radius: 5px;
                  box-shadow: inset var(--box-shadow);
                  // text-shadow: var(--box-shadow);
                  cursor: pointer;
                  transition: all .3s;
                  &:hover {
                    color: black;
                    box-shadow: var(--box-shadow);
                  }
                }
              }
            }
            .userInfo-containere-connect {
              padding-right: 1vw;
              font-size: 2.3vw;
              position: absolute;
              right: 0;
              color: white;
              font-weight: bolder;
            }
          }
        }
      }
    }
    .home-container-main {
      margin: 6% 3%;
    }
  }
}
@primary-color: #FF2A42;