.demo-item {
  position: relative;
  padding-bottom: 40%;
  margin-bottom: 6%;
}

.DemoItem {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  display: flex;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: var(--box-shadow);
  .DemoItem-banner {
    flex: 1;
    height: 100%;
    background-color: white;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .DemoItem-info {
    height: 100%;
    width: 50%;
    background-color: var(--deep-blue);
    box-sizing: border-box;
    padding: 2%;
    color: white;
    .DemoItem-info-title {
      display: flex;
      .DemoItem-info-title-userAvatar {
        width: 12%;
        padding-bottom: 12%;
        position: relative;
        // height: auto;
        border-radius: 10px;
        background-color: var(--main-color);
        overflow: hidden;
        img {
          position: absolute;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      .DemoItem-info-title-text {
        margin-left: 3%;
        box-sizing: border-box;
        padding: 1vw;
        display: flex;
        align-items: center;
        border-radius: 0.1rem;
        // max-width: 88%;
        background-color: var(--main-color);
        font-size: 1.8vw;
      }
    }
    .DemoItem-info-text {
      margin: 0.1rem 0;
      font-size: 1.8vw;
      pre {
        word-wrap: break-word;       
        white-space: pre-wrap;
        line-height: 2.8vw;
      }    
    } 
  }
}
@primary-color: #FF2A42;